const db = wx.cloud.database();
export default{
	data(){
		return{
			avatarUrl:'../../static/icon/avatar.png',//头像
			nickName:'请授权登录',
			token:'',
			buttonShow: false,
			appoinList: []
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.getUserInfo()
		this.token = uni.getStorageSync('token');
		this.isLogin = uni.getStorageSync('isLogin')
		const openid = uni.getStorageSync('openid')
		db.collection('appointment').where({_openid:openid}).get().then(res => {
			// res.data 包含该记录的数据
			this.appoinList = res.data;
			console.log(res);
		});
	},
	methods:{
		getUserInfo(e) {
		    // console.log(e)
		    // 获取用户信息
			let that = this
		    wx.getSetting({
		      success(res) {
		        // console.log("res", res)
		        if (res.authSetting['scope.userInfo']) {
		          console.log("已授权=====")
				  that.buttonShow = false;
		          // 已经授权，可以直接调用 getUserInfo 获取头像昵称
		          wx.getUserInfo({
		          	success: function (res) {
		          		//获取到的微信头像 昵称 性别
		          		that.nickName = res.userInfo.nickName;
		          		that.avatarUrl = res.userInfo.avatarUrl;
		          		
		          	}
		          });
		        } else {
		          console.log("未授权=====")
				  that.buttonShow = true;
		        }
		      }
		    })
		  },
		  toDetail(id){
			  uni.navigateTo({
			  	url:"/pages/appoin/detail?_id=" + id
			  })
		  }
	}
}
